import React, { Fragment } from "react";

import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";

import Button from "@mui/material/Button";

import Typography from "@mui/material/Typography";
import Table from "@mui/material/Table";
import TableHead from "@mui/material/TableHead";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";

import Link from "@mui/material/Link";

import { stringIsMarkdownForHyperlink } from "../../Logic/stringIsMarkdownForHyperlink";
import { stringIsMarkdownForEmail } from "../../Logic/stringIsMarkdownForEmail";

import config from "../../config.json";

const { dataSettings, baseURLforScripts, baseURLforData } = config;
const { display } = dataSettings;

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",

  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,

  overflow: "auto",
  height: "75%",
  width: "75%",
  display: "block",
};

function AdditionalInfo({
  open,
  handleClose,
  columnsOnAdditionalInfo,
  colForTitleForAdditionalInfoPage,
  datum,
}) {
  // Helpers for rendering

  // Render the title
  const renderTitle = (title) => {
    return (
      <Fragment>
        Additional Details for <code>{title}</code>
      </Fragment>
    );
  };

  const renderLink = (stringIsHyperlink, display, i) => {
    const [full, title, url] = stringIsHyperlink;
    return (
      <TableRow
        key={i}
        sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
      >
        <TableCell>
          <b>{display}</b>
        </TableCell>

        <TableCell>
          {" "}
          <Link href={url} variant="body2" target="_blank" rel="noreferrer">
            {title}
          </Link>
        </TableCell>
      </TableRow>
    );
  };

  const renderEmail = (stringIsEmail, display, i) => {
    const [full, title, url] = stringIsEmail;
    return (
      <TableRow
        key={i}
        sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
      >
        <TableCell>
          <b>{display}</b>
        </TableCell>

        <TableCell>
          {title} (
          <Link href={`mailto:${url}`} variant="body2">
            {url}
          </Link>
          )
        </TableCell>
      </TableRow>
    );
  };

  // Render an individual row of the table
  const renderListItemCodeRow = (datum, display, i) => {
    const stringIsEmail = stringIsMarkdownForEmail(datum);
    const stringIsHyperlink = stringIsMarkdownForHyperlink(datum);

    if (stringIsEmail) {
      return renderEmail(stringIsEmail, display, i);
    }

    if (stringIsHyperlink) {
      return renderLink(stringIsHyperlink, display, i);
    }
    return (
      <TableRow
        key={i}
        sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
      >
        <TableCell>
          <b>{display}</b>
        </TableCell>

        <TableCell>{datum}</TableCell>
      </TableRow>
    );
  };

  const renderLookups = (lookups) => {
    return (
      <Fragment>
        <Typography variant="h6">Lookups</Typography>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Permissible Value</TableCell>
                <TableCell>Permissible Value Meaning</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {lookups.map((row, i) => (
                <TableRow
                  key={i}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                >
                  <TableCell>{row.lookup_PV}</TableCell>
                  <TableCell>{row.lookup_PVM}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Fragment>
    );
  };

  const renderLUobs = (lu_obs) => {
    return (
      <Fragment>
        <Typography variant="h6">Observed Lookups</Typography>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Permissible Value</TableCell>
                <TableCell> Permissible Value Meaning</TableCell>
                <TableCell> Count</TableCell>
                <TableCell> Percentage</TableCell>
                <TableCell> Percentage (with missing)</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {lu_obs.map((row, i) => (
                <TableRow
                  key={i}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                >
                  <TableCell>{row.lu_obs_PV}</TableCell>
                  <TableCell>{row.lookup_PVM}</TableCell>
                  <TableCell>{row.lu_obs_n}</TableCell>
                  <TableCell>{`${
                    Math.round(10000 * row.lu_obs_no_missing) / 100
                  }%`}</TableCell>
                  <TableCell>{`${
                    Math.round(10000 * row.lu_obs_pct_w_missing) / 100
                  }%`}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Fragment>
    );
  };

  const renderLinkToData = (table_name) => {
    const nameOfDataFile = `${table_name}.csv`;
    const linkToDataFile = `${baseURLforData}/${nameOfDataFile}`;

    return (
      <Fragment>
        <Typography variant="h6">Download the Parent CSV File </Typography>

        <Typography variant="body">
          Permission to access the BLSA Data Science SharePoint Online site is
          required.
        </Typography>

        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Card>
              <CardContent>
                <a href={`${linkToDataFile}`} download={`${nameOfDataFile}`}>
                  {`${table_name}.csv`}
                </a>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Fragment>
    );
  };

  const renderLinksToScripts = (table_name) => {
    const baseURL = baseURLforScripts;

    const nameOfRScript = `${table_name}.r`;
    const linkToRScript = `${baseURL}/import-data-r/${nameOfRScript}`;

    const nameOfSasScript = `${table_name}.sas`;
    const linkToSasScript = `${baseURL}/import-data-sas/${nameOfSasScript}`;

    const nameOfStataScript = `${table_name}.do`;
    const linkToStataScript = `${baseURL}/import-data-stata/${nameOfStataScript}`;

    return (
      <Fragment>
        <Typography variant="h6">Code to Load and Label Parent CSV </Typography>
        <Grid container spacing={2}>
          <Grid item xs={4}>
            <Card>
              <CardContent>
                <a href={`${linkToRScript}`} download={`${nameOfRScript}`}>
                  R
                </a>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={4}>
            <Card>
              <CardContent>
                <a href={`${linkToSasScript}`} download={`${nameOfSasScript}`}>
                  SAS
                </a>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={4}>
            <Card>
              <CardContent>
                {" "}
                <a
                  href={`${linkToStataScript}`}
                  download={`${nameOfStataScript}`}
                >
                  Stata
                </a>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Fragment>
    );
  };

  // Main rendering

  if (datum) {
    const { lookups, lu_obs } = datum;
    const { table_name } = datum;
    const { obs_in_data } = datum;
    console.log({ obs_in_data });
    console.log(obs_in_data === 1);
    console.log({ baseURLforScripts });
    console.log(baseURLforScripts !== null);
    /*
    const nameOfSasScript = table_name.replace(".csv", ".sas");
    const linkToSasScript = `./import-data-sas/${nameOfSasScript}`;
    */

    return (
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            {renderTitle(datum[colForTitleForAdditionalInfoPage])}
          </Typography>
          <Stack spacing={2}>
            <TableContainer component={Paper}>
              <Table sx={{ minWidth: 650 }} aria-label="simple table">
                <TableBody>
                  {columnsOnAdditionalInfo.map((col, i) => {
                    if (datum[col] != null) {
                      return renderListItemCodeRow(datum[col], display[col], i);
                    } else {
                      return null;
                    }
                  })}
                </TableBody>
              </Table>
            </TableContainer>

            {lookups ? renderLookups(lookups) : null}
            {lu_obs ? renderLUobs(lu_obs) : null}

            {baseURLforData ? renderLinkToData(table_name) : null}

            {/*renderLinkToSasScript(nameOfSasScript, linkToSasScript)*/}
            {(baseURLforScripts !== null) & obs_in_data
              ? renderLinksToScripts(table_name)
              : null}
          </Stack>
        </Box>
      </Modal>
    );
  } else {
    return null;
  }
}

export default AdditionalInfo;
