import * as React from "react";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { Typography } from "@mui/material";
import Stack from "@mui/material/Stack";
import Modal from "@mui/material/Modal";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import ChildModal from "./ChildModal";

import Tooltip from "@mui/material/Tooltip";
import Grid from "@mui/material/Grid";

import { useSelector } from "react-redux";

import CartView from "./CartView";

import NoItemsReturned from "./CartView/NoItemsReturned";

import config from "../config.json";

const { dataSettings } = config;
const { uid } = dataSettings;

const theme = createTheme({
  palette: {
    neutral: {
      main: "#fff",
      contrastText: "#fff",
    },
  },
});

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 650,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

export default function BasicModal() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const idsOfItemsInCart = useSelector((state) => state.idsOfItemsInCart);

  if (idsOfItemsInCart.length === 0) {
    return (
      <div>
        <ThemeProvider theme={theme}>
          <Tooltip title="View Cart">
            <IconButton
              color="neutral"
              aria-label="add to shopping cart"
              onClick={handleOpen}
            >
              <ShoppingCartIcon />
            </IconButton>
          </Tooltip>
        </ThemeProvider>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <NoItemsReturned></NoItemsReturned>
          </Box>
        </Modal>
      </div>
    );
  }

  return (
    <div>
      <ThemeProvider theme={theme}>
        <Tooltip title="View Cart">
          <IconButton
            color="neutral"
            aria-label="add to shopping cart"
            onClick={handleOpen}
          >
            <ShoppingCartIcon />
          </IconButton>
        </Tooltip>
      </ThemeProvider>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Stack spacing={1}>
            <Grid container alignItems="center">
              <Grid item xs={11}>
                <Typography variant="h4">Cart</Typography>
              </Grid>
              <Grid item xs={1}>
                <ChildModal></ChildModal>
              </Grid>
            </Grid>
            <Stack direction="row"></Stack>
            <CartView></CartView>
          </Stack>
        </Box>
      </Modal>
    </div>
  );
}
